.container{
  max-width: 1140px;
  margin: auto;
}

.heading{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #26272b;
  box-shadow: 0px 0px 12px #18191b;
  border-radius: 8px;
  margin: 2rem 1rem;
  padding: .7rem 1rem;
  font-weight: 700;
}

.coin-name{
  margin-left: -4rem;

}

.coin-row{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #26272b;
  box-shadow: 0px 0px 12px #18191b;
  border-radius: 8px;
  margin: 2rem 1rem;
  padding: .7rem 1rem;
  transition: transform .4s ease-in-out; /* 将过渡效果应用于默认状态 */
  cursor: pointer; 
}

.coin-row:hover{
  transform: scale(1.06);

}

img{
  height: 40px;
  margin-right: 8px;
  width: auto
}

.img-symbol{
  display: flex;
  align-items: center;
}


@media screen and (max-width: 720px) {
  .hide-mobile{
    display: none;
  }
}